* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  font-size: 15px;
  background: url("https://cdn.pixabay.com/photo/2015/05/11/17/47/music-762842_960_720.jpg")
    center right no-repeat;
  background-size: cover;
}

.drum-machine {
  width: 90%;
  max-width: 40rem;
  margin: 15rem auto;
  padding: 0.5rem;
  background: #0000001c;
  text-align: center;
}

.toggleActive {
  width: 6rem;
  margin: 0.7rem;
  border: none;
}

.toggleActive span {
  display: inline-block;
  width: 50%;
  padding: 5px;
  background: #3e565b;
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 600;
}

#display {
  width: 12rem;
  height: 2.6rem;
  margin: 0.7rem auto;
  border: 2px solid #34565b;
  background: #d6f2f63b;
  color: #3e565b;
  text-transform: uppercase;
  font-size: 1.5rem;
  font-weight: 600;
}

